.container {
  position: absolute;
  overflow: hidden;
  top: 0;
  left: 20px;
  right: 20px;
  bottom: 0;
  padding: 16px;
  border: 10px solid #000;
  align-items: center;
}

.cards-container {
  position: fixed;
  overflow: scroll;
  height: 700px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 16px;
}


.flip-card {
   width: 150px;
   height: 250px;
   border: 1px solid #f1f1f1;
   perspective: 1000px;
   border-radius: 10px;
   margin-right: 20px;
}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 0.8s;
  transform-style: preserve-3d;

}


.flip-card-front, .flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;

}

.flip-card-back{
  background-color: #bb3030;
  border-radius: 10px;
}

.flip-card-back img {
  height: 70px;
  position: relative;
  top: 80px;
  left: 40px;
}

.win-label {
  text-align: center;
}

.container h1 {
  text-align: center;
  color: #40af40
}